import React from 'react'
import Countdown from '../../components/countdown';
import './index.css'

const Coming = (props) => {
  const { showCountdown = true } = props
  return (
    <React.Fragment>
      {
        showCountdown ? (
          <div className="coming-soon-wrapper">
            <Countdown />
          </div>
        ) : null
      }
      <div className="coming-soon-placeholder"></div>
    </React.Fragment>
  )
}

export default Coming